import React, { useState, useEffect } from "react";

// import Cart from "./CartItems";

import {BrowserRouter, Route, Link,NavLink} from "react-router-dom";

import StarRating from "./Stars";

function Shop(){
    let data = [{
        id:"1",
        itemName : "Fancy Product",
        sellStatus: "",
        itemOldPrice :"",
        itemNewPrice :"$40.00 - $80.00",
    },
    {
        id:"2",
        itemName : "Special Item",
        sellStatus: "Sale",
        itemOldPrice :"$20.00",
        itemNewPrice :"$18.00",
    },
    {        
        id:"3",
        itemName : "Sale Item",
        sellStatus: "Sale",
        itemOldPrice :"$50.00",
        itemNewPrice :"$25.00",
    },
    {        
        id:"4",
        itemName : "Popular Item",
        sellStatus: "",
        itemOldPrice :"",
        itemNewPrice :"$40.00",
    },
    {        
        id:"5",
        itemName : "Sale Item",
        sellStatus: "Sale",
        itemOldPrice :"$50.00",
        itemNewPrice :"$25.00",
    },
    {        
        id:"6",
        itemName : "Fancy Product",
        sellStatus: "",
        itemOldPrice :"",
        itemNewPrice :"$120.00 - $280.00",
    },
    {
        id:"7",
        itemName : "Special Item",
        sellStatus: "Sale",
        itemOldPrice :"$20.00",
        itemNewPrice :"$18.00",
    },
    {
        id:"8",
        itemName : "Popular Item",
        sellStatus: "",
        itemOldPrice :"",
        itemNewPrice :"$40.00",
    }
]

    const [count, setState] = useState({
        cart:'0',
        disabled: [],
    })
    const addCart = (id) =>{
        setState({...count, cart: + count.cart + +1, disabled: [...count.disabled, id]});
    }

    const clearCart = () => {
        // console.log(count.disabled.length)
        count.disabled.splice(0, count.disabled.length)
        setState({...count, cart:count.disabled.length});
    }

    
    
    return(
        <>
        {/* <ShopHead value={count.cart} disabledVal={count.disabled}/> */}
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <div class="container px-4 px-lg-5">
                <a class="navbar-brand" href="#!">Start Bootstrap</a>
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"><span class="navbar-toggler-icon"></span></button>
                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav me-auto mb-2 mb-lg-0 ms-lg-4">
                        <li class="nav-item"><a class="nav-link active" aria-current="page" to="/">Home</a></li>
                        <li class="nav-item"><a class="nav-link" href="#!">About</a></li>
                        <li class="nav-item dropdown">
                            <a class="nav-link dropdown-toggle" id="navbarDropdown" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">Shop</a>
                            <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><a class="dropdown-item" href="#!">All Products</a></li>
                                <li><hr class="dropdown-divider" /></li>
                                <li><a class="dropdown-item" href="#!">Popular Items</a></li>
                                <li><a class="dropdown-item" href="#!">New Arrivals</a></li>
                            </ul>
                        </li>
                    </ul>
                    {/* <form class="d-flex"> */}
                    {/* <Link to="/cart"> */}
                        <button class="btn btn-outline-dark">
                            <i class="bi-cart-fill me-1"></i>
                             Cart                            
                            <span class="badge bg-dark text-white ms-1 rounded-pill">
                                {count.cart}
                            </span>
                        </button>
                        <button class="hover-hide btn btn-outline-dark" title="Clear Cart" onClick={clearCart}>Clear Cart</button>
                    {/* </Link> */}
                    
                    {/* </form> */}
                </div>
            </div>
        </nav>
        <header class="bg-dark py-5">
            <div class="container px-4 px-lg-5 my-5">
                <div class="text-center text-white">
                    <h1 class="display-4 fw-bolder">Shop in style</h1>
                    <p class="lead fw-normal text-white-50 mb-0">With this shop hompeage template</p>
                </div>
            </div>
        </header>
        <section class="py-5">
            <div class="container px-4 px-lg-5 mt-5">
                <div class="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">
                    {data.map((items)=>{
                        return(
                    <div class="col mb-5">
                        <div class="card h-100">
                            
                            <div class="badge bg-dark text-white position-absolute" >{items.sellStatus}</div>
                            
                            <img class="card-img-top" src="https://dummyimage.com/450x300/dee2e6/6c757d.jpg" alt="..." />
                            
                            <div class="card-body p-4">
                                <div class="text-center">
                                    
                                    <h5 class="fw-bolder">{items.itemName}</h5>
                                    
                                    <StarRating/>
                                     
                                    <span class="text-muted text-decoration-line-through">{items.itemOldPrice} </span>
                                    {items.itemNewPrice}
                                </div>
                            </div>
                            
                            <div class="card-footer p-4 pt-0 border-top-0 bg-transparent">
                                <button disabled={count.disabled.indexOf(items.id)!==-1} class="text-center btn btn-outline-dark mt-auto"  onClick={()=>addCart(items.id)} >Add to cart</button>
                            </div>
                        </div>
                    </div>
                        )
                    })}
                </div>
            </div>
        </section>
        <footer class="py-5 bg-dark">
            <div class="container"><p class="m-0 text-center text-white">Copyright &copy; Your Website 2021</p></div>
        </footer>

        </>
    )
}
export default Shop;